<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header school='东北大学'></Header>
      <ProjectTitle :data="projectInfo"></ProjectTitle>
      <TeamTab :data="tabData" :active-index="1" style="display: none"></TeamTab>
      <div class="container">
        <div class="bar">
          <div class="title">{{teamInfo.teamName}}</div>
          <el-form :inline="true" class="demo-form-inline pull-right">
            <el-form-item>
              <span> 当前人数 </span>
              <span>{{teamInfo.currentMemberCount}}人</span>
              <span> 申请人数 </span>
              <span>{{teamInfo.applyMemberCount}}人</span>
            </el-form-item>
            <el-form-item>
              <div v-if="myJoinType === -1">
                <el-button v-if="myJoinStatus !== 1" type="primary" @click="applyJoin(teamInfo.teamId)">申请加入</el-button>
                <el-button v-else>已申请</el-button>
              </div>
              <div v-if="myJoinType === 1 && myJoinStatus=== 0">
                <el-button>已申请</el-button>
              </div>
              <div v-if="myJoinType === 0">
                <el-button type="primary" v-if="myJoinStatus === 0"  @click="handleInvite(teamInfo.teamId, 1)">同意</el-button>
                <el-button  v-if="myJoinStatus === 0"  @click="handleInvite(teamInfo.teamId, -1)">拒绝</el-button>
              </div>
            </el-form-item>
            <el-form-item v-if="myJoinStatus === 1">
              <el-button @click="deleteMe">退出</el-button>
            </el-form-item>
            <el-form-item v-if="(myJoinStatus === -1 && myJoinType === 1) || (myJoinType === -1) || (myJoinStatus === 0 && myJoinType === 1) || (myJoinStatus === -1 && myJoinType === 0)">
              <el-button @click="goBack">返回</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="main">
          <div class="member-list">
            <div class="box member-box" v-for="obj in teamInfo.memberList" v-bind:key="obj.userCode">
              <div class="name">{{obj.userName}}</div>
              <div class="position">{{obj.teamRole === 'leader' ? '队长' : '队员'}}</div>
              <div class="head-portrait"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      title="创建团队"
      :visible.sync="createTeamDialogVisible"
      width="468px"
      center>
      <div class="content">
        <el-form ref="createTeamForm" :model="createTeamForm" label-width="90px">
          <div class="head-portrait" :style="{ backgroundImage: 'url('+ createTeamForm.logoUrl + ')'}">
            <div class="model" @click="$refs.headPortrait.click()">上传头像</div>
            <input style="display:none" type="file" name="fileUpload" @change="uploadPhoto($event)" ref="headPortrait" >
          </div>
          <el-form-item class="mt-5" label="团队名称：">
            <el-input v-model="createTeamForm.name"></el-input>
          </el-form-item>
          <el-form-item label="团队人数：">
            <span v-if="projectInfo.minCount && projectInfo.maxCount">{{projectInfo.minCount}}-{{projectInfo.maxCount}}人</span>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button style="width:198px;" type="primary" @click="createTeamSave">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import ProjectTitle from '../../../../components/school-training/ProjectTitle.vue'
import TeamTab from '../../../../components/school-training/TeamTab.vue'
export default {
  components: {
    Header,
    ProjectTitle,
    TeamTab
  },
  name: 'app',
  mounted () {
    this.myJoinType = parseInt(localStorage.getItem('myJoinType'))
    this.myJoinStatus = parseInt(localStorage.getItem('myJoinStatus'))
    this.loadData()
  },
  data () {
    return {
      projectInfo: {},
      createTeamDialogVisible: false,
      createTeamForm: {
        logoUrl: '../../assets/img/default-head.png',
        name: ''
      },
      searchKey: '',
      tabData: [{
        name: '选择项目',
        url: 'course-detail.html'
      }, {
        name: '组队',
        url: 'team-search.html'
      }, {
        name: '任务裁剪',
        url: ''
      }, {
        name: '人员管理',
        url: ''
      }],
      teamInfo: {},
      teamData: [],
      myJoinType: '',
      myJoinStatus: ''
    }
  },
  methods: {
    goBack () {
      // history.back()
      window.location = 'team-search.html'
    },
    loadData () {
      this.loadTeamInfo()
      this.loadProjectInfo()
    },
    loadTeamInfo () {
      let me = this
      let teamId = localStorage.getItem('teamId')
      this.$http.post('/teambuild/queryteaminfoformember', {
        teamId: teamId
      }, res => {
        if (res.success) {
          me.teamInfo = res.data
        }
      })
    },
    loadProjectInfo () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      this.$http.post('/teambuild/queryprojectinfo', {
        projectId: projectId,
        courseId: courseId
      }, res => {
        if (res.success) {
          me.projectInfo = res.data
          me.projectInfo.courseName = localStorage.getItem('courseName')
        }
      })
    },
    createTeam () {
      this.createTeamDialogVisible = !this.createTeamDialogVisible
      // window.location = 'team-my.html'
    },
    createTeamSave () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      let grade = localStorage.getItem('grade')
      this.$http.post('/teambuild/buildteam', {
        logoUrl: this.createTeamForm.logoUrl,
        teamName: this.createTeamForm.name,
        courseId: courseId,
        projectId: projectId,
        grade: grade
      }, res => {
        if (res.success) {
          me.createTeamDialogVisible = false
          window.location = 'team-my.html'
        }
      })
    },
    applyJoin (teamId) {
      let me = this
      this.$http.post('/teambuild/applyjointeam', {
        teamId: teamId
      }, res => {
        if (res.success) {
          me.$alert('已提交申请！', '提示', {
            confirmButtonText: '确定'
          })
          me.myJoinStatus = 0
          me.myJoinType = 1
          localStorage.setItem('myJoinStatus', 0)
          localStorage.setItem('myJoinType', 1)
          me.loadData()
        }
      })
    },
    uploadPhoto (event) {
      let me = this
      let file = event.target.files[0]
      let formData = new FormData()
      formData.append('file', file)
      this.$http.uploadFile('/teambuild/uploadimgforteam', formData, res => {
        if (res.success) {
          me.$message('上传成功')
          me.createTeamForm.logoUrl = res.data
        } else {
          // 返回错误的处理
        }
      })
    },
    goTeam (teamId) {
      localStorage.setItem('teamId', teamId)
      window.location = 'team-view.html'
    },
    handleInvite (teamId, joinStatus) {
      let me = this
      let courseId = localStorage.getItem('courseId')
      let projectId = localStorage.getItem('projectId')
      me.$http.post('/teambuild/agreerefuseinvitemember', {
        teamId: teamId,
        joinStatus: joinStatus
      }, res => {
        if (res.success) {
          if (joinStatus === 1) {
            // me.$message('已接受邀请！')
            me.$alert('已接受邀请！', '提示', {
              confirmButtonText: '确定'
            })
            // .then(({ value }) => {
            // window.location = 'personal-main.html'
            // })
            localStorage.setItem('myJoinStatus', parseInt(joinStatus))
            me.myJoinStatus = 1
            me.loadData()
          } else {
            me.$alert('已拒绝邀请！', '提示', {
              confirmButtonText: '确定'
            })
            me.$http.post('/campuspage/querycourseprojectlist', {
              'courseId': courseId
            }, resproject => {
              if (resproject.success) {
                // console.log(res.data)
                for (var i = 0; i < resproject.data.length; i++) {
                  if (parseInt(projectId) === resproject.data[i].projectId) {
                    if (resproject.data[i].teamId === teamId || resproject.data[i].teamId === 0) {
                      window.location = 'team-search.html'
                    } else {
                      let teamIdNew = resproject.data[i].teamId
                      me.$http.post('/teambuild/teamIdentityInfo', {
                        teamId: teamIdNew
                      }, respersonal => {
                        localStorage.setItem('teamId', teamIdNew)
                        localStorage.setItem('myJoinStatus', respersonal.data.joinStatus)
                        localStorage.setItem('myJoinType', respersonal.data.joinType)
                        me.myJoinStatus = respersonal.data.joinStatus
                        me.myJoinType = respersonal.data.joinType
                        me.loadTeamInfo()
                        // window.location = 'team-view.html'
                      })
                    }
                  }
                }
              } else {
                // 返回错误的处理
              }
            })
            // window.location = 'team-search.html'
          }
        }
      })
    },
    deleteMe () {
      let me = this
      this.$confirm('是否退出团队?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        me.$http.post('/teambuild/deleteteambuildmember', {
          teamId: me.teamInfo.teamId,
          userCode: me.teamInfo.userCode,
          teamRole: '0'
        }, res => {
          if (res.success) {
            me.$message('退出成功！')
            window.location = 'team-search.html'
          }
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .team-box{
    position:relative;
    float:left;
    padding-top:22px;
    padding-bottom:19px;
    width:171px;
    background:rgba(245,245,245,1);
    border:1px solid rgba(235, 235, 235, 1);
    border-radius:5px;
    text-align: center;
    margin-right:40px;
    margin-bottom:40px;
    margin-top:24px;
    .logo{
      width:105px;
      height:105px;
      background:rgba(218,218,218,1);
      border-radius:50%;
      margin: 0 auto;
    }
    .name{
      font-size:14px;
      font-family:PingFang-SC-Bold;
      font-weight:bold;
      color:rgba(102,102,102,1);
      margin-top:14px;
    }
    .count{
      font-size:12px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      margin-top:8px;
    }
    .el-button{
      margin-top:5px;
      padding:8px 23px;
    }
  }
  .head-portrait{
    margin: 0px auto;
    width:116px;
    height:116px;
    background-image: url("../../assets/img/default-head.png");
    background-size: 100% 100%;
    border-radius:58px;
    .model{
      width:100%;
      height:100%;
      background-color: #000000;
      border-radius:58px;
      opacity:0.5;
      display:none;
      z-index:101;
      text-align: center;
      padding-top:45px;
      font-size:14px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(255,255,255,1);
      cursor:pointer;
    }
  }
  .head-portrait:hover{
    .model{
      display:block;
    }
  }
  .member-list{
    width:100%;
    display: inline-block;
    margin-top:37px;
    .box{
      position:relative;
      float:left;
      width:173px;
      height:131px;
      border-radius:5px;
      margin-right:37px;
    }
    .add-box{
      background:rgba(245,245,245,1);
      border:1px solid rgba(235, 235, 235, 1);
      text-align: center;
      padding-top:20px;
      cursor:pointer;
      i{
        font-size:43px;
        color:#999999;
      }
      span{
        margin-top:15px;
        display: block;
        font-size:14px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(102,102,102,1);
      }
    }
    .member-box{
      background:rgba(140,48,255,1);
      box-shadow:0px 3px 20px 0px rgba(127,72,255,0.7);
      div{
        position: absolute;
        z-index:100;
      }
      .name{
        top: 21px;
        left: 9px;
        font-size:18px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        color:rgba(255,255,255,1);
      }
      .position{
        top: 46px;
        left: 10px;
        font-size:12px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
      .step{
        top: 89px;
        left: 10px;
        font-size:12px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
      .head-portrait{
        width:68px;
        height:69px;
        border-radius:50%;
        background-color: #FFFFFF;
        top: 10px;
        right: 11px;
      }
      .model{
        width:100%;
        height:100%;
        background-color: #000000;
        border-radius:5px;
        opacity:0.65;
        display:none;
        z-index:101;
      }
      .action{
        width:100%;
        height:100%;
        display:none;
        z-index:102;
        text-align: center;
        padding-top:40px;
        i{
          font-size:29px;
          color:#FF4C55;
          cursor:pointer;
        }
        span{
          margin-top:7px;
          display: block;
          font-size:12px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(255,255,255,1);
        }
      }
    }
    .member-box:hover{
      .model,.action{
        display:block;
      }
    }
  }
</style>
